body {
  @media only screen and (max-width: 819px) {
    .uppy-Dashboard--modal .uppy-Dashboard-inner {
      top: 90px;
    }
  }

  .uppy-Dashboard--modal .uppy-Dashboard-AddFiles {
    border: none;
  }

  .uppy-Dashboard-inner{
    background-color: var(--bs-body-bg);
  }

  .uppy-size--md .uppy-DashboardTab-btn {
    background-color: var(--bs-gray-100);
    margin: 0 2px;
  }

  .uppy-Dashboard-AddFiles-title,
  .uppy-DashboardContent-title,
  .uppy-Webcam-title{
    color: var(--bs-gray-800);
  }

  .uppy-DashboardContent-panel {
    background-color: var(--bs-gray-100);
}

.uppy-DashboardContent-bar{
  background-color: var(--bs-body-bg);
  border-bottom: 1px solid var(--bs-gray-300);
}
.uppy-DragDrop-container:focus{
  box-shadow:none;
}
.uppy-c-textInput,
.uppy-DragDrop--isDragDropSupported{
  color: var(--bs-body-color);
  background-color: var(--bs-body-bg);
  border: var(--bs-border-width) solid var(--bs-border-color);
}
.uppy-DragDrop--isDragDropSupported{
  border-style: dashed;
}
.uppy-c-btn-primary{
  background-color: var(--bs-primary);
}

.uppy-DashboardContent-back:focus, .uppy-DashboardContent-save:focus{
  background-color: var(--bs-gray-100);

}

  .uppy-DashboardTab-inner {
    background-color: transparent;
    box-shadow: none;
  }

  .uppy-size--md .uppy-Dashboard-AddFiles-title, .uppy-Dashboard-AddFiles-title {
    font-size: 14px;
    position: relative;
    margin-top: 100px;
  }
  .uppy-Dashboard-innerWrap {
    max-height: calc(100vh - 150px);
  }
  .uppy-Dashboard-inner{
    max-height: calc(100% - 150px);
  }
  
  @media only screen and (min-width: 820px) {
    .uppy-Dashboard-AddFiles-title:after {
      content: "";
      position: absolute;
      height: 70px;
      width: 70px;
      left: 0;
      right: 0;
      margin: 0 auto;
      top: -90px;
      background-repeat: no-repeat;
      background-size: contain;
      background-image: url('data:image/svg+xml;charset=utf-8;base64,');
    }
  }
}

.uppy_scale .uppy-Dashboard-AddFiles-title:after{
  display: none;
}

.uppy_scale .uppy-Dashboard-AddFiles-title{
  margin-top: 10px;
}

.uppy_scale .uppy-Dashboard-innerWrap{
  max-height: 100%;
}

.uppy_scale .uppy-Dashboard-AddFiles-list{
    overflow: hidden;
}