:root {
    --toastify-color-info: var(--bs-info);
    --toastify-color-success: var(--bs-success);
    --toastify-color-warning: var(--bs-warning);
    --toastify-color-error:  var(--bs-danger);
}

[data-theme=dark]{
    .Toastify__toast {
        background: #000;
        color: var(--bs-gray-800);
    }
    .Toastify__close-button{
        color: var(--bs-gray-800);
    }
}


.Toastify__toast{
    min-height: 50px;
    margin-bottom: 0;
}

.Toastify__close-button{
    align-self: center;
}