.sk_rect{
    height: 45px;
    width: 100%;
    background: var(--bs-gray-200);
    border-radius: 3px;
    position: relative;
    overflow: hidden;
    background-color: var(--bs-gray-200) !important;
}
.sk_img{
    height: 75px;
    width: 75px;
    background: var(--bs-gray-200);
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    background-color: var(--bs-gray-200) !important;
}

.sk_line:before, .sk_img:before, .sk_rect::before{
    left: 0;
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    //background-image: linear-gradient(to right, var(--bs-gray-200) 0%, rgba(0,0,0,0.05) 20%, var(--bs-gray-200) 40%, var(--bs-gray-200) 100%);
    background: linear-gradient(90deg, var(--bs-gray-200) 25%, var(--bs-skeleton) 50%, var(--bs-gray-200) 75%);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    animation: shimmer 1.5s linear infinite;
}

.sk_img:before {
    background-size: 650px 600px;
}

@keyframes shimmer {
    0%{
      background-position: -300px 0;
    }
    100%{
      background-position: 300px 0;
    }
}

.sk_img_thumb {
    min-height: 150px;
    background: var(--bs-gray-200);
    position: relative;
}

.m_grid_author .sk_img {
