.offer_strip {
       z-index: 999;
       right: 0;
       left: 0;
       max-width: 570px;
       bottom: 0;
       box-shadow: 0 0 3.6rem rgba(0, 0, 0, 0.12);
}

@media (min-width: 991px) {
       .offer_strip {
              left: 83px;
       }
}

@keyframes fadeInUp {
       0% {
              transform: translateY(100%);
              opacity: 0;
       }

       100% {
              transform: translateY(0%);
              opacity: 1;
       }
}

.offer_strip.show {
       animation: 1s fadeInUp;
}


.header-fixed.header-mobile-fixed #ugc-suit-influencer .subheader {
       top: 50px;
       background-color: var(--bs-body-bg) !important;
}
