.auth_bg{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.bg-gradient-1{
    background: radial-gradient(30.69% 108.71% at 29.63% 82.87%, rgba(239, 221, 184, 0.4) 0%, rgba(252, 254, 254, 0.4) 100%) ,
radial-gradient(35.13% 35.18% at 73.15% 17.18%, rgba(163, 119, 235, 0.4) 0%, rgba(238, 252, 252, 0.4) 100%);

}

.register_check{
    padding-left: 0;
    list-style: none;
}
.auth_form_area{
    max-width: 500px;
    margin: 0 auto;
    width: 100%;
}

.view_pass{
    right: -10px;
}

.suite_logo svg path{
    fill: var(--bs-light);
}

.white_transparent,
.white_transparent .h-40px{
    background-color: rgba(var(--bs-primary-rgb),.25);
}
.auth_form{
    min-height: calc(100vh - 110px);
}

@media (min-width: 992px){
    // .auth_form_area{
    //     max-width: 450px;
    // }
    .auth_form{
        min-height: 100vh;
    }
    .auth_full_screen .auth_form{
        min-height: calc(100vh - 66px);
    }
}

